/* You can add global styles to this file, and also import other style files */
ul {
  list-style: none;
  padding-inline-start: 0;
  padding-left: 0;
  margin: 0;
}

.hidden {
  visibility: hidden;
}

.shadow {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: black;
  opacity: .4;
}
